import * as React from 'react';
import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import BackupTableSharpIcon from '@mui/icons-material/BackupTableSharp';
import SchemaSharpIcon from '@mui/icons-material/SchemaSharp';

import erd_data from './erd_data.json'
import erd_link from './erd_link.json'

const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white,
    },
    [`&.${tableCellClasses.body}`]: {
        fontSize: 14,
    }
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover,
    },
    // hide last border
    '&:last-child td, &:last-child th': {
        border: 0,
    },

}));

const rows = erd_data


export default function CustomizedTables() {
    return (
        <>
            <Box sx={{ p: 2 }}>
                <Typography variant="h1" sx={{ fontSize: '40px' }}>
                    ERD Frisianflag
                </Typography>
            </Box>
            <Box display="flex"
                justifyContent="right"
                alignItems="right"
                sx={{ pl: 2 }}>
                <IconButton
                    href={erd_link.link}
                    target='__blank'
                    size="large"
                    edge="start"
                    color="inherit"
                    aria-label="open drawer"
                    sx={{ mr: 2 }}
                >
                    <Typography sx={{ fontSize: '20px' }}>
                        Entity Relationship Diagram
                    </Typography>
                    <SchemaSharpIcon />
                </IconButton>

            </Box>
            <Box display="flex"
                justifyContent="center"
                alignItems="center"
                sx={{ pb: 1 }}>
                <Typography variant="h2" sx={{ fontSize: '35px' }}>
                    Table Documentation
                </Typography>
            </Box>

            <Box sx={{ pl: 4, pr: 4 }}>
                <TableContainer component={Paper}>
                    <Table sx={{ minWidth: 700 }} aria-label="customized table">
                        <TableHead>
                            <TableRow>
                                <StyledTableCell align='center' sx={{ fontSize: '18px' }}><strong>Table Name</strong></StyledTableCell>
                                <StyledTableCell align='center' sx={{ fontSize: '18px' }}><strong>Description</strong></StyledTableCell>
                                <StyledTableCell align='center' sx={{ fontSize: '18px' }}><strong>Architecture</strong></StyledTableCell>

                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows.map((row) => (
                                <StyledTableRow key={row.table_name}>
                                    <StyledTableCell size='small' component="th" scope="row">
                                        {row.table_name}
                                    </StyledTableCell>
                                    <StyledTableCell size='small' align='left'>
                                        {row.description}
                                    </StyledTableCell>
                                    <StyledTableCell size='small' align="center">
                                        <IconButton
                                            href={row.link}
                                            target='__blank'
                                            size="small"
                                            edge="start"
                                            color="inherit"
                                            aria-label="open drawer"
                                            sx={{ mr: 2 }}
                                        >
                                            <BackupTableSharpIcon />
                                        </IconButton>
                                    </StyledTableCell>

                                </StyledTableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Box>
        </>
    );
}