import * as React from 'react';
import { styled, alpha } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import InputBase from '@mui/material/InputBase';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';

import Drawer from '@mui/material/Drawer';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import HomeIcon from '@mui/icons-material/Home';
import Collapse from '@mui/material/Collapse';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import FolderIcon from '@mui/icons-material/Folder';
import TurnedInSharpIcon from '@mui/icons-material/TurnedInSharp';
import { Outlet } from "react-router-dom"



const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing(1),
    width: 'auto',
  },
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  width: '100%',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    [theme.breakpoints.up('sm')]: {
      width: '12ch',
      '&:focus': {
        width: '20ch',
      },
    },
  },
}));

const CustomDrawer = styled(Drawer)(({ theme }) => ({
  '& .MuiDrawer-paper': {
    color: 'white',
    backgroundColor: 'rgba(1,61,121,255)'
  },
  '& .MuiListItemIcon-root': {
    color: 'white'
  }
}));

export default function SearchAppBar() {
  const [open, setOpen] = React.useState(false);

  const [openCussons, setOpenCussons] = React.useState(false)
  const handleClickCussons = () => { setOpenCussons(!openCussons) };
  const [openFrisianflag, setOpenFrisianflag] = React.useState(false)
  const handleClickFrisianflag = () => { setOpenFrisianflag(!openFrisianflag) };

  const toggleDrawer = (newOpen) => () => {
    setOpen(newOpen);
  };

  const DrawerList = (
    <Box sx={{ width: 350 }} role="presentation">
      <List>
        <ListItemText primary={'IFRS17 Data Management'} sx={{ pl: 5 }} />
      </List>
      <Divider sx={{ backgroundColor: 'white' }} />
      <List>
        <ListItemButton href='home'>
          <ListItemIcon>
            <HomeIcon />
          </ListItemIcon>
          <ListItemText primary={'Home'} />
        </ListItemButton>
        <ListItemButton onClick={handleClickCussons}>
          <ListItemIcon>
            <FolderIcon />
          </ListItemIcon>
          <ListItemText primary="Cussons" />
          {openCussons ? <ExpandLess /> : <ExpandMore />}
        </ListItemButton>
        <Collapse in={openCussons}>
          <List>
            <ListItemButton sx={{ pl: 5 }}>
              <ListItemIcon>
                <TurnedInSharpIcon />
              </ListItemIcon>
              <ListItemText primary={'Blast Check'} />
            </ListItemButton>
          </List>
        </Collapse>
        <ListItemButton onClick={handleClickFrisianflag}>
          <ListItemIcon>
            <FolderIcon />
          </ListItemIcon>
          <ListItemText primary="Frisianflag" />
          {openFrisianflag ? <ExpandLess /> : <ExpandMore />}
        </ListItemButton>
        <Collapse in={openFrisianflag}>
          <List>
            <ListItemButton sx={{ pl: 5 }} href='frisianflag-erd'>
              <ListItemIcon>
                <TurnedInSharpIcon />
              </ListItemIcon>
              <ListItemText primary={'Entity Relationship Diagram'} />
            </ListItemButton>
          </List>
        </Collapse>
      </List>
    </Box>
  );
  return (
    <>
      <Box sx={{ flexGrow: 1, }}>
        <AppBar position="static" sx={{ backgroundColor: 'rgba(1,61,121,255)' }}>
          <Toolbar variant="dense">
            <IconButton
              size="small"
              edge="start"
              color="inherit"
              aria-label="open drawer"
              sx={{ mr: 2 }}
            >
              <MenuIcon onClick={toggleDrawer(true)} />
            </IconButton>
            <Typography
              variant="h6"
              noWrap
              component="div"
              sx={{ flexGrow: 1, display: { xs: 'none', sm: 'block' } }}
            >
              IFRS17 Data Management
            </Typography>
            <Search>
              <SearchIconWrapper>
                <SearchIcon />
              </SearchIconWrapper>
              <StyledInputBase
                placeholder="Search"
                inputProps={{ 'aria-label': 'search' }}
              />
            </Search>
          </Toolbar>
        </AppBar>
        <CustomDrawer open={open} onClose={toggleDrawer(false)}>
          {DrawerList}
        </CustomDrawer>
      </Box>
      <Outlet/>
    </>
  );
}